import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addErrors, createUser } from '../../../store/usersSlice';
import YellowButton from '../../buttons/YellowButton';
import Progress from '../../progress/progress';
import ErrorText from '../../text/errorText';
import Wrap from '../../wrap';
import FileInput from './fileInput';
import InputText from './InputText';
import PositionsList from './positionsList';
import { checkEmail, checkName, checkPhone, checkPosition } from './validation';
import { ReactComponent as ImageSuccess } from '../../../Assets/success-image.svg';

const RegistrationForm = () => {
  const dispatch = useDispatch();
  const { isCreatedSuccess, status, error } = useSelector((store) => store.users);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [file, setFile] = useState(null);
  const [checkBoxValue, setCheckBoxValue] = useState(null);

  const [isSignUp, setSignUp] = useState(false);

  const changeFileUpload = (evt) => {
    const files = evt.target.files;

    setFile(files.length > 0 ? files[0] : null);
  };

  const signUpHandler = (evt) => {
    evt.stopPropagation();
    evt.preventDefault();

    //Validation
    const errors = {
      fails: {
        name: checkName(name),
        email: checkEmail(email),
        phone: checkPhone(phone),
        position: checkPosition(checkBoxValue),
      },
    };

    if (errors.fails.name || errors.fails.email || errors.fails.phone || errors.fails.position) {
      errors.message = 'Validation failed';
      dispatch(addErrors(errors));
    } else {
      //      
      const formData = new FormData();
      formData.append('position_id', checkBoxValue);
      formData.append('name', name);
      formData.append('email', email);
      formData.append('phone', phone);
      formData.append('photo', file);
      dispatch(createUser(formData));
    }
  };

  useEffect(() => {
    const prevVisible = isSignUp;
    let isVisible = true;

    if (name.length === 0) isVisible = false;
    if (email.length === 0) isVisible = false;
    if (phone.length === 0) isVisible = false;
    if (!file) isVisible = false;

    if (prevVisible !== isVisible) setSignUp(isVisible);
  }, [name, phone, email, file, isSignUp]);

  console.log('Registration render');
  return (
    <Wrap
      sx={{
        marginBottom: '100px',
      }}
    >
      <Typography id='signup' variant='h1' component='h1'>
        Working with POST request
      </Typography>
      {isCreatedSuccess ? (
        <ImageSuccess style={{ marginTop: '50px' }} />
      ) : (
        <Box
          component='form'
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'left',
            width: { mobile: 328, tablet: 380 },
            marginTop: '50px',
          }}
        >
          <InputText
            value={name}
            setValue={setName}
            title='Your name'
            error={!!error?.fails?.name}
            helperText={error?.fails?.name?.join('\n')}
          />
          <InputText
            type='email'
            value={email}
            setValue={setEmail}
            title='Email'
            error={!!error?.fails?.email}
            helperText={error?.fails?.email?.join('\n')}
          />
          <InputText
            type='tel'
            value={phone}
            setValue={setPhone}
            title='Phone'
            error={!!error?.fails?.phone}
            helperText={error?.fails?.phone?.join('\n')}
          />

          <PositionsList checkBoxValue={checkBoxValue} setCheckBoxValue={setCheckBoxValue} />

          <FileInput
            onChange={changeFileUpload}
            error={!!error?.fails?.photo}
            helperText={error?.fails?.photo?.join('\n')}
          />

          {status === 'pending' ? (
            <Progress
              sx={{
                alignSelf: 'center',
                marginTop: '50px',
              }}
            />
          ) : (
            <YellowButton
              type='submit'
              onClick={signUpHandler}
              disabled={!isSignUp}
              sx={{
                alignSelf: 'center',
                marginTop: '50px',
              }}
            >
              Sign up
            </YellowButton>
          )}

          {error && <ErrorText sx={{ alignSelf: 'center' }}>{error?.message}</ErrorText>}
        </Box>
      )}
    </Wrap>
  );
};

export default RegistrationForm;
